@font-face {
    font-family: calibri;
    src: url(../../static/media/Calibri-Regular.5d7c31b2.ttf); 
}
@font-face {
    font-family: calibri-bolde;
    src: url(../../static/media/Calibri-Bold.527c5ab6.ttf); 
    src:  url(../../static/media/Calibri-Bold.7b7e5f5b.eot) format('embedded-opentype'),
    url(../../static/media/Calibri-Bold.527c5ab6.ttf) format('truetype'),
    url(../../static/media/Calibri-Bold.bcff7ce6.woff) format('woff'),
    url(../../static/media/Calibri-Bold.4c20fe4e.svg) format('svg');
