.wallet-button {
	display: inline-block;
	color: #fff;
	text-decoration: none;
	background: -webkit-gradient(linear, left top, left bottom, from(#5CB0FE), to(#0A5185));
	background: -webkit-linear-gradient(#5CB0FE, #0A5185);
	background: linear-gradient(#5CB0FE, #0A5185);
	border-radius: 50px;
	position: relative;
	z-index: 1;
	width: 200px;
	text-align: center;
	height: 40px;
	line-height: 30px;
	overflow: hidden;
	border: none;
	outline: none;
	font-size: 14px;
}
.wallet-button:hover {
	background: -webkit-gradient(linear, left top, left bottom, from(#2F7BC2), to(#63BEFF));
	background: -webkit-linear-gradient(#2F7BC2, #63BEFF);
	background: linear-gradient(#2F7BC2, #63BEFF);
}
.wallet-button:before {
	content: "";
	display: block;
	border-radius: 50px;
	position: absolute;
	background: inherit;
	top: 1px;
	left: 1px;
	right: 1px;
	bottom: 1px;
	z-index: -1;
}
.wallet-button:after {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	z-index: -2;
	-webkit-transform: rotate(0deg);
	        transform: rotate(0deg);
	-webkit-animation: gradient 2s ease alternate infinite;
	        animation: gradient 2s ease alternate infinite;
	-webkit-animation-play-state: paused;
	animation-play-state: paused;
	background: -webkit-gradient(linear, left top, left bottom, from(#D4EDFF), to(#2F79BE));
	background: -webkit-linear-gradient(#D4EDFF, #2F79BE);
	background: linear-gradient(#D4EDFF, #2F79BE);
}
