.t-tle.wallet__def-item {
	margin-bottom: 10px;
}
.t-tle.wallet__def-item .t-cell:nth-child(4) {
	text-align: right;
	opacity: 0;
}
.t-tle.wallet__def-item .t-cell:nth-child(5) {
	margin-left: 10px;
	padding-left: 10px;
	opacity: 0;
}

.wallet__item .wallet__def-item:hover .t-cell:nth-child(4) {
	opacity: 1;
}
.wallet__item .wallet__def-item:hover .t-cell:nth-child(5) {
	opacity: 1;
}

.wallet__item .wallet__def-item:hover {
	box-shadow: 0 0 10px rgba(56, 159, 255, 0.5);
}

.wallet__item .wallet__def-item:hover .wallet-item__def-adress {
	color: #99CEFF;
}
.wallet__item .wallet__def-item:hover p.wallet-item__def-tit {
	color: white;
}
.wallet__item .wallet__def-item:hover p.wallet-item__wallet-curr {
	color: #99CEFF;
}

.wallet__bg-block_arrow.wallet__item-arrow {
	margin-top: 0;
	margin-bottom: 0;
	padding-bottom: 0;
	/*opacity: 0;*/
}
.wallet__item:hover .wallet__bg-block_arrow.wallet__item-arrow {
	opacity: 1;
}
.t-tle.wallet__def-item.wallet__sec-item {
	margin-left: 50px;
	width: calc(100% - 50px);
}


.t-tle.wallet__def-item.wallet__sec-item .t-cell:nth-child(2) {
	/*background: red;*/
	/*width: 20%;*/
}
.t-tle.wallet__def-item.wallet__sec-item .wallet-item__def-adress {
	text-overflow: ellipsis;
	width: 290px;
	white-space: nowrap; 
    overflow: hidden;
}
.t-tle.wallet__def-item .wallet-item__def-adress {
	width: 340px;
}

.wallet-content__transaction-nav {
	display: block;
	height: 30px;
	margin-bottom: 15px;
}
.__workspace_start-list.__transaction-list {
	margin-top: 0;
}
.wallet__trans-sort_select {
	margin-right: 5px;
}
.wallet__trans-sort_select .btn.dropdown-toggle {
	border: 1px solid #345274;
	border-radius: 50px;
	height: 30px;
	width: 170px;
	background: transparent;
	color: #99CEFF;
	text-transform: uppercase;
	font-family: roboto-regular;
	font-size: 10px;
	outline: none!important;

}
.wallet__trans-sort_select .btn.dropdown-toggle:focus {
	background: #003D69!important;
	border: 1px solid #345274;
	color: #99CEFF;
	outline: none!important;
}
.wallet__trans-sort_select.open .btn.dropdown-toggle {
	background: #003D69!important;
	border: 1px solid #345274;
	color: #99CEFF;
	border-radius: 15px 15px 0px 0px;
	border: 1px solid transparent;
}
.wallet__trans-sort_select.open:hover .btn.dropdown-toggle {
	background: #003D69!important;
	color: #99CEFF;
}
.wallet__trans-sort_select.bootstrap-select:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
	width: auto;
}

.wallet__trans-sort_select.open .dropdown-menu.open {
	border-radius: 0px 0px 15px 15px;
	margin: 0;
	padding: 0;
	border: none;
}
.wallet__trans-sort_select.open .dropdown-menu.open:hover {
	line-height: 25px;

}
.wallet__trans-sort_select.open .dropdown-menu.open li {
	background: #003D69;
	color: white;
}
.wallet__trans-sort_select.open .dropdown-menu.open li.selected a {
	background: #002F54;
}
.wallet__trans-sort_select.open .dropdown-menu.open li a {
	color: #99CEFF;
	font-size: 10px;
	text-transform: uppercase;
	height: 30px;
	line-height: 25px;
	border-radius: 30px;
	outline: none;
	padding-left: 15px;
}
.wallet__trans-sort_select.open .dropdown-menu.open li a:hover {
	background: #00538e;
	padding-left: 15px;
}
.wallet__trans-sort_select.open .dropdown-menu.open li.selected a:focus {
	outline: none;
}

/* currency list start */
.currency-box__table {
	min-height:70px;
	margin:4px 0;
	display:table;
	width:100%;
	margin:20px 0;
	border: 1px solid transparent;
}

.currency-box__cell {
	display: table-cell;
	background:rgba(10, 73, 130, 0.5);
	vertical-align:middle;
}


.currency-box__cell:first-child {
	border-radius: 35px 0 0 35px;
	padding: 0 15px 0 35px;
	width: 5%;
}

.currency-box__cell:last-child {
	border-radius: 0 35px 35px 0;
	min-width: 50px;
}

.currency-box__cell.__starred_items a { cursor: pointer; }

.currency-box__cell.__starred_items span[class^="icon-"] {
    line-height: 24px;
    color: #37628d;
    opacity: 0.8;
    font-size: 18px;
}

.currency-box__table:hover .currency-box__cell.__starred_items span[class^="icon-"] {
    color: #5c94c1;
    opacity: 1;
}

.currency-box__table:hover .currency-box__cell.__starred_items a:hover span[class^="icon-"] {
