@font-face {
    font-family: calibri;
    src: url(../fonts/Calibri-Regular.ttf); 
}
@font-face {
    font-family: calibri-bolde;
    src: url(../fonts/Calibri-Bold.ttf); 
    src:  url('../fonts/Calibri-Bold.eot') format('embedded-opentype'),
    url('../fonts/Calibri-Bold.ttf') format('truetype'),
    url('../fonts/Calibri-Bold.woff') format('woff'),
    url('../fonts/Calibri-Bold.svg') format('svg');
}
body.body__videovote {
	background: white;
}
.videovote {
	padding-top: 90px;
	width: 924px;
	margin: 0 auto;
	font-family: calibri;
	color: #222;
}

.videovote__left {
	width: 50%;
	float: left;
}
.videovote__tit {
	font-family: calibri-bolde;
	font-size: 32px;
	line-height: 32px;
	margin-bottom: 15px;
}
.videovote__link {
	color: #0094FF;
	font-size: 14px;
}
.videovote__link:hover {
	text-decoration: none;
	color: #0094FF;
	display: inline-block;
}
.videovote__link:after {
	content: "";
	display: table;
	clear: both;
}
.videovote__player {
	margin-top: 35px;
	margin-bottom: 30px;
	display: block;
}
.videovote__how-tit {
	font-family: calibri-bolde;
	font-size: 24px;
	margin-bottom: 10px;
}
.videovote__list {
	padding: 0;
	margin: 0;
	margin-bottom: 10px;
}
.videovote__list li {
	list-style-type: none;
	font-family: calibri-bolde;
	font-size: 16px;
	padding-left: 21px;
	position: relative;
	margin-bottom: 2px;
}
.videovote__list li:before {
	content: "";
	position: absolute;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	left: 0px;
	top: 6px;
}
.videovote__list li:first-child:before {
    background: linear-gradient(180deg, #39C948 0%, #1E9F2C 100%);
}
.videovote__list li:last-child:before {
    background: linear-gradient(180deg, #FB5E5B 0%, #D91F1B 100%);
}
.videovote__area-tit {
	font-size: 16px;
}
.videovote__overarea {
	height: 55px;
	border-radius: 5px;
	overflow-y: hidden;
}
.videovote__area-btn {
	background: linear-gradient(180deg, #0094FF 0%, #0076CB 100%);
	float: right;
	height: 55px;
	line-height: 55px;
	color: white;
	width: 150px;
	padding: 0;
	font-size: 16px;
	border: none;
	box-shadow: none;
	outline: none;
}
.videovote__area-btn:hover {
	background: linear-gradient(200deg, #0094FF 0%, #0076CB 100%);
}
.videovote__area-btn:active {
	background: linear-gradient(180deg, #0094FF 0%, #0076CB 100%);
}
.videovote__area-btn svg {
	margin-top: 19px;
	margin-left: 15px;
	float: left;
}
.videovote__area-btn span {
	display: inline-block;
	margin-left: -10px;
}
.videovote__area {
	border: 1px solid #ebebeb;
	border-right: none;
	resize: none;
	margin: 0;
	height: 100%;
	width: calc(100% - 150px);
	border-radius: 5px 0 0 5px;
	box-shadow: none;
	outline: none;
	padding: 6px 20px;
	font-size: 16px;
	font-family: calibri-bolde;
	overflow: hidden;
}


.videovote__right {
	float: right;
	width: 50%;
	padding-left: 80px;
}
.videovote__right-tit {
	font-size: 24px;
	font-family: calibri-bolde;
	margin-top: 32px;
	margin-bottom: 10px;
}


.videovote__progress {
    height: 12px;
    line-height: 15px;
    border-radius: 20px;
    background: #f0f0f0;
    margin-bottom: 5px;
    box-shadow: none;
    overflow: visible;
    background: linear-gradient(180deg, #FB5E5B 0%, #D91F1B 100%);
}
.videovote__progress .progress-bar {
    position: relative;
    border-radius: 20px 0 0 20px;
    animation: animate-positive 2s;
	background: linear-gradient(180deg, #39C948 0%, #1E9F2C 100%);
    box-shadow: none;
}
.videovote__progress .progress-value {
    display: block;
    font-size: 13px;
    color: #fff;
    position: absolute;
    top: 0;
    right: 8px;
}
@-webkit-keyframes animate-positive {
    0% { width: 0%; }
}
@keyframes animate-positive { 
    0% { width: 0%; }
}
.videovote__afterprog {
	margin-bottom: 14px;
}
.videovote__afterprog {
	font-size: 16px;
	font-family: calibri-bolde;
	text-transform: uppercase;
}
.videovote__afterprog.no {
	float: right;
	color: #FB5E5B;
}
.videovote__afterprog.yes {
	color: #39C948;
}
.videovote__overtab {
	max-height: 420px;
	overflow: auto;
}
.videovote__table {
	width: 100%;
	font-size: 12px; 
	border-collapse:separate;
    border-spacing:0 5px;

}
.videovote__table td:nth-child(2) {
	/*text-overflow: ellipsis;  */
}
.videovote__table tr.come {
	background: #D7F4DA;
}
.videovote__table tr.gone {
	background: #FEDFDE;
}
.videovote__table td {
	color: #636363;
	height: 20px;
	vertical-align: middle;
	padding-left: 10px;
	padding-right: 10px;
	padding-top: 4px;
}
.videovote__table td:first-child {
	border-radius: 5px 0 0 5px;
}
.videovote__table td:nth-child(2) {
	/*text-align: center;*/
}
.videovote__table td:last-child {
	border-radius: 0px 5px 5px 0;
	text-align: right;
}
.videovote__table tr.come td:last-child {
	color: #39C948;
}
.videovote__table tr.gone td:last-child {
	color: #FB5E5B;
}











